body {
  background-color: #333;
}

.container {
  background-color: whitesmoke;
  width: 50%;
  margin: 0 auto;
  margin-top: 25px;
  padding: 0px;
  border: 1px solid #000;
  box-shadow: 0 0 10px white;
  border-radius: 8px;
  text-align: center;
}

h1 {
  margin-bottom: 20px;
  font-size: 2rem;
  color: #000;
}

.contact-info {
  font-size: 1.2rem;
}

.contact-info p {
  margin: 10px 0;
  color: #000;
  font-size: large;
}

.contact-info a {
  color: black;
  text-decoration: none;
  font-size: large;
}

.contact-info a:hover {
  text-decoration: underline;
  color: black;
}

@media (max-width: 768px) {
  .container {
    width: 90%;
    padding: 10px;
  }

  h1 {
    font-size: 1.5rem;
  }

  .contact-info {
    font-size: 1rem;
  }
}
